import { IconButton } from '@material-ui/core';
import { MenuOpen } from '@material-ui/icons';
import React, { useEffect, useState } from 'react';
import { Nav, NavBarContainer, NavLogo, MobileIcon, NavMenu, NavLinks, NavItem } from './navBarStyles';

const NavBar = ({toggleOpen}) => {
    
    const [scrollNav, setScrollNav] = useState(false);

    const changeNav = () => {
      if(window.scrollY >= 80){
          setScrollNav(true);
      }else{
          setScrollNav(false);
      }
    };


    useEffect(() => {
        window.addEventListener('scroll', changeNav)
    }, [])

    return (
        <>
            <Nav scrollNav={scrollNav}>
                <NavBarContainer>
                    <NavLogo to='/' >
                        Home
                    </NavLogo>
                    <MobileIcon onClick={toggleOpen}>
                            
                    </MobileIcon>
                    <NavMenu>
                        <NavItem>
                            <NavLinks to="cv" >Currículum</NavLinks>
                        </NavItem>
                        <NavItem>
                            <NavLinks to="portfolio" >Proyectos</NavLinks>
                        </NavItem>
                        <NavItem>
                            <NavLinks to="contact" >Contacto</NavLinks>
                        </NavItem>
                    </NavMenu>
                </NavBarContainer>
            </Nav>
        </>
    )
}

export default NavBar
